<template>
    <div class="container">
      <div class="sidebar">
        <h2></h2>
        <ul>
          <li><router-link to="/menu"></router-link></li>
          <li><router-link to="/coupon"></router-link></li>
          <li><router-link to="/ordertable"></router-link></li>
          <li><router-link to="/package"></router-link></li>
          <li><router-link to="/orders"></router-link></li>
          <li><router-link to="/takeaway-order"></router-link></li>
          <li><router-link to="/reviews"></router-link></li>
          <li><router-link to="/exchange"></router-link></li>
        </ul>
      </div>
  
      <div class="content">
        <div class="membership-info">
          <h2></h2>
          <div v-if="user">
            <p>ID{{ user.userId }}</p>
            <p>{{ user.username }}</p>
            <p>{{ user.memberLevel }}</p>
            <p>{{ user.memberBalance }}</p>
            <p>{{ user.memberPoints }}</p>
          </div>
          <div v-else>
            <p></p>
          </div>
        </div>
  
        <div class="membership-actions">
          <h2></h2>
          <form @submit.prevent="submitRecharge">
            <label for="amount"></label>
            <input type="text" id="amount" v-model="rechargeAmount" placeholder="" />
            <span class="tooltip-icon" @click="toggleInfoBox">!</span>
            <button type="submit"></button>
          </form>
  
          <!--  -->
          <div v-if="showInfoBox" class="info-box">
            <p class="info-text">1.500 VIP11000 VIP22000 VIP3</p>
            <p class="info-text">2.VIP1VIP2VIP3</p>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data() {
      return {
        user: null,
        rechargeAmount: '',
        showInfoBox: false,
        statusMessage: '', // 
      };
    },
    created() {
      const username = localStorage.getItem("username");
      if (username) {
        this.loadUserInfo(username);
      } else {
        alert("");
        this.$router.push("/login");
      }
    },
    methods: {
      async loadUserInfo(username) {
        try {
          const response = await axios.get(`http://localhost:8080/api/user/${username}`);
          if (response.data.status === "success") {
            this.user = response.data.user;
          } else {
            alert(response.data.message || "");
          }
        } catch (error) {
          console.error("", error);
          alert("" + error);
        }
      },
  
      async submitRecharge() {
        if (isNaN(this.rechargeAmount) || parseFloat(this.rechargeAmount) <= 0) {
          alert("");
          return;
        }
  
        try {
          const response = await axios.post('http://localhost:8080/api/recharge', {
            username: localStorage.getItem("username"),
            amount: parseFloat(this.rechargeAmount),
          });
  
          if (response.data.status === "success") {
            alert("");
            this.loadUserInfo(localStorage.getItem("username")); // 
          } else {
            this.statusMessage = response.data.message || "";
          }
        } catch (error) {
          console.error(':', error);
          alert("");
        }
      },
  
      toggleInfoBox() {
        this.showInfoBox = !this.showInfoBox;
      },
    },
  };
  </script>
  
  <style scoped>
  /*  */
  body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background-image: url("./img/background.jpg");
    background-size: cover;
    color: black;
    background-attachment: fixed;
  }
  
  .container {
    display: flex;
    max-width: 1200px;
    margin: 40px auto;
    padding: 40px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .sidebar {
    background-color: rgba(255, 255, 255, 0.8);
    padding: 20px;
    width: 200px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .sidebar h2 {
    margin-bottom: 10px;
    font-size: 20px;
    color: #2980b9;
    border-bottom: 2px solid #2980b9;
    padding-bottom: 10px;
  }
  
  .sidebar ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .sidebar ul li {
    margin-bottom: 10px;
  }
  
  .sidebar ul li a {
    text-decoration: none;
    color: #333;
    font-size: 16px;
    transition: color 0.3s ease;
  }
  
  .sidebar ul li a:hover {
    color: #2980b9;
    font-weight: bold;
  }
  
  /*  */
  .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    max-width: 1100px; /*  */
    margin: 0 auto; /*  */
  }
  
  /*  */
  .membership-info,
  .membership-actions {
    background-color: rgba(255, 255, 255, 0.95);
    padding: 30px;
    border-radius: 12px;
    box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.1);
    width: 950px;
    margin-bottom: 25px;
  }
  
  /*  */
  .membership-info h2,
  .membership-actions h2 {
    margin-bottom: 15px;
    font-size: 22px;
    color: #2980b9;
    border-bottom: 2px solid #2980b9;
    padding-bottom: 8px;
  }
  
  /*  */
  .membership-info p {
    margin-bottom: 10px;
    font-size: 16px;
  }
  
  /*  */
  .membership-actions form {
    margin-top: 10px;
  }
  
  .membership-actions label {
    display: block;
    margin-bottom: 8px;
    font-size: 16px;
  }
  
  .membership-actions input[type="text"] {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    margin-top: 5px;
    border: 1px solid #ccc;
    border-radius: 6px;
    box-sizing: border-box;
  }
  
  .membership-actions button {
    margin-top: 15px;
    padding: 10px 20px;
    font-size: 16px;
    background-color: #3498db;
    color: #fff;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .membership-actions button:hover {
    background-color: #2980b9;
  }
  
  /*  icon */
  .tooltip-icon {
    display: inline-block;
    margin-left: 10px;
    font-weight: bold;
    cursor: pointer;
    color: #3498db;
  }
  
  /*  */
  .info-box {
    margin-top: 10px;
    background-color: #ecf0f1;
    padding: 10px;
    border-radius: 6px;
    font-size: 8px;
    color: #2c3e50;
    line-height: 1.5;
  }
  .info-text {
  font-size: 16px; /*  */
    }
  </style>
  